@font-face{
  font-family:"Bachelor Pad Shadow JL";
  src:url("./Bachelor Pad Shadow JL.ttf") format("truetype");
}
.App {
  text-align: center;
  margin: 100px 0px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1#heading{
 font-family:"Bachelor Pad Shadow JL";
  font-size: 4rem;
  color: white;
}